export default {
	data() {
		return {
			id:'',
			sessionId:'',
			myData:[]
		}
	},
	
	onLoad(option) {
		this.sessionId = option.sessionId
		this.init()
	},
	
	methods: {
		init(){
			uni.request({
				url:'https://www.nssnail.com/wx/personal/getAuctionRecord',
				// method:'POST',
				data:{
					sessionId:this.sessionId
				},
				success: (res) => {
					if(res.data.stat == 1){
						this.myData = res.data.data
						this.myData.forEach((item)=>{
							if(item.stat == 1){
								item.stat = '未拍卖'
							}else if(item.stat == 2){
								item.stat = '拍卖中'
							}else if(item.stat == 3){
								item.stat = '已拍卖'
							}else{
								item.stat = '失效'
							}
						})
					}else if(res.data.stat == 3){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
					}
				}
			})
		},
		
		// 跳转至详情页
		gotoNoticeDetail(item){
			var id = item.id
			uni.navigateTo({
				url:'../donateNoticeDetail/donateNoticeDetail?id='+id
			})
		}
	}
}
